import React from 'react';
import PropTypes from 'prop-types';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Switch from '@material-ui/core/Switch';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import FavoriteIcon from '@material-ui/icons/Favorite';
import '../css/Todo.css';
import Tooltip from '@material-ui/core/Tooltip';
import Fade from '@material-ui/core/Fade';
import BorderColorIcon from '@material-ui/icons/BorderColor';

const Todo = ({ onToggle, onToggleFavourite, onRemove, title, description, completed, modifiedDate, isFavourite, color }) => {
    return (
        <>
            <div className={"card shadow " + color}>
                <div className="card-body">
                    <h5 className="card-title">{title}</h5>
                    <p className="card-text card-body-text">{description}</p>
                    <div className="row">
                        <div className="col-md-12">
                            <Tooltip TransitionComponent={Fade} TransitionProps={{ timeout: 600 }} title="Complete task">
                                <Switch
                                    checked={completed}
                                    onChange={onToggle}
                                    color="primary"
                                    name="ckbComplete"
                                />
                            </Tooltip>
                            <Tooltip TransitionComponent={Fade} TransitionProps={{ timeout: 600 }} title={isFavourite ? "Remove from favourites" : "Add to favourites"}>
                                <IconButton aria-label="favoutite" color="secondary" onClick={onToggleFavourite}>
                                    {isFavourite ? <FavoriteIcon /> : <FavoriteBorderIcon />}
                                </IconButton>
                            </Tooltip>
                            <Tooltip TransitionComponent={Fade} TransitionProps={{ timeout: 600 }} title="Remove task">
                                <IconButton aria-label="remove" onClick={onRemove}>
                                    <DeleteIcon color="error" />
                                </IconButton>
                            </Tooltip>
                            <Tooltip TransitionComponent={Fade} TransitionProps={{ timeout: 600 }} title="Edit task">
                                <IconButton aria-label="edit" onClick={onRemove}>
                                    <BorderColorIcon color="primary" />
                                </IconButton>
                            </Tooltip>
                        </div>
                    </div>
                </div>
                <div className="card-footer text-muted">
                    <small>Last updated on : {modifiedDate}</small>
                </div>
            </div>

        </>
    )
}

// Todo.propTypes = {
//     onClick: PropTypes.func.isRequired,
//     completed: PropTypes.bool.isRequired,
//     text: PropTypes.string.isRequired
// }

export default Todo;
