import { ADD_TODO, TOGGLE_TODO, REMOVE_TODO, TOGGLE_FAVOURITE_TODO } from '../actions/todoActions'

const todos = (state = [], action) => {
    switch (action.type) {
        case ADD_TODO:
            return [
                ...state,
                {
                    id: action.payload.id,
                    title: action.payload.title,
                    description: action.payload.description,
                    modifiedDate: new Date().toUTCString(),
                    completed: false,
                    isFavourite: action.payload.isFavourite,
                    color:action.payload.color
                }
            ];
        case TOGGLE_TODO:
            return state.map(todo =>
                todo.id === action.payload.id ? { ...todo, completed: !todo.completed,modifiedDate: new Date().toUTCString() } : todo)
        case TOGGLE_FAVOURITE_TODO:
            return state.map(todo =>
                todo.id === action.payload.id ? { ...todo, isFavourite: !todo.isFavourite,modifiedDate: new Date().toUTCString() } : todo)
        case REMOVE_TODO:
            return state.filter(todo =>
                todo.id !== action.payload.id)
        default:
            return state;
    }
}

export default todos;