// action types

export const ADD_TODO = "ADD_TODO"
export const TOGGLE_TODO = "TOGGLE_TODO"
export const TOGGLE_FAVOURITE_TODO = "TOGGLE_FAVOURITE_TODO"
export const REMOVE_TODO = "REMOVE_TODO"
export const UPDATE_TODO = "REMOVE_TODO"
export const SET_VISIBILITY_FILTER = "SET_VISIBILITY_FILTER"


// constants

export const VisibilityFilters = {
    SHOW_ALL: 'SHOW_ALL',
    SHOW_COMPLETED: 'SHOW_COMPLETED',
    SHOW_ACTIVE: 'SHOW_ACTIVE'
}

// action creators

let id = 0;

export const AddToDo = (title, description, isFavourite, color) => ({
    type: ADD_TODO,
    payload: {
        id: ++id,
        title,
        description,
        isFavourite,
        color
    }
})

export const ToggleToDo = id => ({
    type: TOGGLE_TODO,
    payload: {
        id
    }
})

export const ToggleFavouriteToDo = id => ({
    type: TOGGLE_FAVOURITE_TODO,
    payload: {
        id
    }
})

export const RemoveToDo = id => ({
    type: REMOVE_TODO,
    payload: {
        id
    }
})

export const SetVisibilityFilter = filter => ({
    type: SET_VISIBILITY_FILTER,
    payload: {
        filter
    }
})