import React from 'react';
import { AddToDo } from '../actions/todoActions';
import { connect } from 'react-redux';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import '../css/AddTodo.css';
import Tooltip from '@material-ui/core/Tooltip';
import Fade from '@material-ui/core/Fade';
import ColorOptions from '../components/ColorOptions.js';

class AddTodo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            done: false
        }
    }


    // const handleChange = (colorName) => {
    //     color = colorName;
    // };
    render() {
        console.log("add to rendered again");
        let txtTitle, txtDescription, ckbFavourite, color = "blue-card";
        return (
            <div className="row mb-2">
                <div className="col-md-12">
                    <Paper className="p-3 pr-4 pl-4 shadow">
                        <form onSubmit={e => {
                            e.preventDefault()
                            if (!txtTitle.value.trim()) {
                                return
                            }
                            if (!txtDescription.value.trim()) {
                                return
                            }

                            this.props.dispatch(AddToDo(txtTitle.value, txtDescription.value, ckbFavourite.checked, color));
                            this.setState({ ...this.state, done: true });
                        }}>
                            <div className="form-group">
                                <TextField
                                    id="txtTitle"
                                    label="Title"
                                    className="form-control"
                                    fullWidth
                                    key={Math.random()}
                                    inputRef={node => (txtTitle = node)} />
                            </div>
                            <div className="form-group">
                                <TextField
                                    id="txtDescription"
                                    label="Description"
                                    className="form-control"
                                    fullWidth
                                    key={Math.random()}
                                    inputRef={node => (txtDescription = node)} />
                            </div>
                            <div className="form-group">
                                <FormControlLabel
                                    key={Math.random()}
                                    control={<Checkbox icon={<FavoriteBorder />}
                                        inputRef={node => (ckbFavourite = node)}
                                        checkedIcon={<Favorite />} name="ckbFavourite" />}
                                    label="Add To Favourites"
                                />
                            </div>
                            <div className="form-group">
                                <ColorOptions
                                    colorValue="blue-card"
                                    handleChange={(colorName) => color = colorName} />
                            </div>
                            <div className="form-group">
                                <Tooltip TransitionComponent={Fade} TransitionProps={{ timeout: 600 }} title="Add task">
                                    <Button
                                        variant="contained"
                                        type="submit"
                                        color="primary"
                                        startIcon={<AddIcon />}>
                                        Task
                                    </Button>
                                </Tooltip>
                            </div>
                        </form>
                    </Paper>
                </div>
            </div>
        )
    }
}

export default connect()(AddTodo);