import React from 'react';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import NoteIcon from '@material-ui/icons/Note';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

class ColorOption extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.state = {
            colorValue: this.props.colorValue
        };
    }
    handleChange(event) {
        this.setState({ ...this.state, colorValue: event.target.value });
        this.props.handleChange(event.target.value);
    }
    render() {
        console.log("color option rendered again");
        return (
            <FormControl component="fieldset">
                <FormLabel component="legend">Select Color : </FormLabel>
                <RadioGroup aria-label="filter" name="filters" className="ml-2" row value={this.state.colorValue} onChange={this.handleChange} >
                    <FormControlLabel className={this.state.colorValue === "blue-card" ? "selected-color" : ""} value="blue-card" control={<Radio style={{ display: 'none' }} />} label={<NoteIcon style={{ color: '#87c0ff' }} />} />
                    <FormControlLabel className={this.state.colorValue === "grey-card" ? "selected-color" : ""} value="grey-card" control={<Radio style={{ display: 'none' }} />} label={<NoteIcon style={{ color: '#b2b4b6' }} />} />
                    <FormControlLabel className={this.state.colorValue === "green-card" ? "selected-color" : ""} value="green-card" control={<Radio style={{ display: 'none' }} />} label={<NoteIcon style={{ color: '#85e69b' }} />} />
                    <FormControlLabel className={this.state.colorValue === "red-card" ? "selected-color" : ""} value="red-card" control={<Radio style={{ display: 'none' }} />} label={<NoteIcon style={{ color: '#f5b3ba' }} />} />
                    <FormControlLabel className={this.state.colorValue === "yellow-card" ? "selected-color" : ""} value="yellow-card" control={<Radio style={{ display: 'none' }} />} label={<NoteIcon style={{ color: '#ffe492' }} />} />
                    <FormControlLabel className={this.state.colorValue === "lovender-card" ? "selected-color" : ""} value="lovender-card" control={<Radio style={{ display: 'none' }} />} label={<NoteIcon style={{ color: '#9ae0eb' }} />} />
                </RadioGroup>
            </FormControl>
        )
    }
}

export default ColorOption;